import NavigationBar from './NavigationBar'
import Footer from './Footer'
import Home from './Home'
import Login from './Login'
import Register from './Register'
import Contact from './Contact'
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom'

function App() {
  return (
    <Router>
      <div className="App">
      <NavigationBar/>
      
      <Switch >

        <Route path="/comeupkings-react/Login" component={Login} />
        <Route path="/comeupkings-react/Register" component={Register} />
        <Route path="/comeupkings-react/Contact" component={Contact} />
        <Route path="/" component={Home}/>
        
      </Switch>

      <Footer/>
      </div>
    </Router>
    
  );
}

export default App;
